<template>
  <div class="app">
    <audio id="player"/>
    <main class="main">
      <div class="container-fluid nopadding">
        <router-view></router-view>
      </div>
    </main>
  </div>
</template>

<script>

let player

import nodemixin from './mixins/nodemixins'
import  EventBus  from './utils/eventBus'

export default {
  name: 'App',
  mixins: [nodemixin],
  methods: {
    test(input){
      console.log('player')
      player.src = 'https://s3-eu-west-1.amazonaws.com/' + process.env.RESOURCEBUCKET + '/public/audio/' + input
      player.play()
    }
  },
  mounted () {
    console.log('mounted')
    EventBus.$on('initAudio', function (payLoad) {
      player = document.getElementById('player')
      player.play()
    });
     EventBus.$on('playAudio', function (input) {
       console.log(input)
      player.src = 'https://s3-eu-west-1.amazonaws.com/' + process.env.RESOURCEBUCKET +'/public/'+ input
      player.play()
    });
  }
}
</script>

<style>

.nopadding {
  padding-left: 0px;
  padding: 0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 98wv;
}
body {
  background-image: url(https://i.pinimg.com/originals/5b/be/e9/5bbee9b08f6d1eafb014269ac137ae1f.jpg);
  background-size: cover;
}
.bg {
  height: 100vh;
  
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: 75% 50%;
}
#controls {
  position: absolute;
  bottom: 20px;
  right: 20px;
}
#control-row {
  height: 50px;
}
#direction {
  width: 50px;
  float: left;
}
#soundarea {
  position: absolute;
  bottom: 22vw;
  left: 6vw;
}
#info {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
}
.language-btn {
  left: 20px;
  outline: none;
  position: absolute;
  bottom: 20px;
  height: 15vw;
  max-height: 150px;
}
.path {
  position: absolute;
  top: 120px;
  right: 10px
}



</style>



